import React from 'react';
import { Bar } from 'react-chartjs-2';

const WasteWeightChart = () => {
  // Sample data for waste weight disposed per hour for 7 days
  const data = {
    labels: Array.from({ length: 7 }, (_, i) => `${i + 1}`),
    datasets: Array.from({ length: 24 }, (_, i) => ({
      label: `${i + 1}h`,
      backgroundColor: `rgba(75,192,${i * 10},0.7)`,
      data: Array.from({ length: 7 }, () => Math.floor(Math.random() * 100)), // Replace with your data
    })),
  };

  const options = {
    scales: {
      x: [
        {
          title: {
            display: true,
            text: 'Day of the Week',
          },
        },
      ],
      y: [
        {
          title: {
            display: true,
            text: 'Waste Weight (lbs)',
          },
        },
      ],
    },
  };

  return (
    <div>
      <Bar data={data} options={options} />
    </div>
  );
};

export default WasteWeightChart;
