import React from 'react';

import 'chart.js/auto'
import { Bar, Line } from 'react-chartjs-2';



const options = {
    scales: {
      x: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Day of the Week',
          },
          ticks: {
            autoSkip: false, // Prevent automatic skipping of x-axis labels
          },
        },
      ],
      y: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Waste Weight (lbs)',
          },
        },
      ],
    },
  };

const labels = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug','Sep','Oct','Nov','Dec'];



export const data = {
  labels,
  datasets: [
    {
      label: 'Baseline (lbs)',
      type: 'line',
      data: [100,100,100,100,100,150,150,150,100,150,150,250],
      backgroundColor: '#ff1744',
    },
    {
      label: 'Weight Entry (lbs)',
      type: 'bar',
      data: [150,50,80,200,100,250,100,300,100,250,300,350],
      backgroundColor: '#2196f3',
    },
  ],
};

const MyChart = () => {
  return (
    <>
      <Bar options={options} data={data} />
    </>
  );
};

export default MyChart;
