import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";

const Charts = () => {
  const [dataLoaded, setDataLoaded] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      // Sample data for 7 days
      const days = ['Day 1', 'Day 2', 'Day 3', 'Day 4', 'Day 5', 'Day 6', 'Day 7'];

      const baselineData = [10, 15, 20, 25, 30, 35, 40]; // Sample Baseline data
      const actualData = [12, 14, 18, 22, 27, 33, 36]; // Sample Actuals data
      const goalData = [8, 18, 25, 30, 28, 40, 38]; // Sample Goal data
      const achievedData = [10, 12, 18, 20, 26, 30, 35]; // Sample Achieved data

      // Set the data
      setDataLoaded(true);
      // Set the data for the charts (similar to Chart.js structure)
      setChartData({
        labels: days,
        datasets: [
          {
            label: "Baseline",
            borderColor: "blue",
            data: baselineData,
          },
          {
            label: "Actuals",
            borderColor: "red",
            data: actualData,
          },
          {
            label: "Goal",
            borderColor: "green",
            data: goalData,
          },
          {
            label: "Achieved",
            borderColor: "orange",
            data: achievedData,
          },
        ],
      });
    };

    fetchData();
  }, []);

  const [chartData, setChartData] = useState({});

  return (
    <div>
      {dataLoaded && (
        <>
          <h5>Baseline vs Actual</h5>
          <Line data={chartData} />

          {/* Similarly, you can add another Line chart for Goal vs Achieved */}
        </>
      )}
    </div>
  );
};

export default Charts;
