import React from 'react';
import { Line } from 'react-chartjs-2';

const MonthlyWasteChart = () => {
  // Sample data for 12 months
  const months = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'
  ];

  const wasteData = [
    100, 80, 120, 90, 110, 70, 130, 100, 85, 95, 105, 75
  ];

  const data = {
    labels: months,
    datasets: [
      {
        label: 'Monthly Waste Trend',
        data: wasteData,
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
      },
    ],
  };

  const options = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  return (
    <div>
      <h2>Monthly Waste Trend</h2>
      <Line data={data} options={options} />
    </div>
  );
};

export default MonthlyWasteChart;
