import React, { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import logo from '../logo.svg';
import { Link, useNavigate } from "react-router-dom";

function GoalSetting() {
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(null);
    const [goalValue, setGoalValue] = useState("");
    const [selectedMetric, setSelectedMetric] = useState("metric1"); // Default metric
    const navigate = useNavigate();

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };
    const openNewTab = () => {
        window.open('/FoodWasteCapture', '_blank');
    };
    const openLink = (link) => {
        navigate(link);
    };
    const handleEndDateChange = (date) => {
        setEndDate(date);
    };

    const handleGoalChange = (event) => {
        setGoalValue(event.target.value);
    };

    const handleMetricChange = (event) => {
        setSelectedMetric(event.target.value);
    };

    const handleSetGoal = () => {
        // Handle setting the goal logic here
        // You can access startDate, endDate, goalValue, and selectedMetric for further processing
        // For example: console.log(startDate, endDate, goalValue, selectedMetric);
        // navigate("/");
    };

    return (
        <div className="App">
            {/* Navbar */}
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>

                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/homepage">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/setup">
                            Setup
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#" onClick={openNewTab}>Food waste capture</a>
                    </li>
                </ul>

                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>

            {/* Goal Setting Form */}
            <div className="container mt-5 d-flex justify-content-center align-items-center">
                <div className="col-md-6">
                    <h2 className="text-center">Goal Setting</h2>
                    <form>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">Start Date:</label>
                            <div className="col-sm-8">
                                <DatePicker
                                    selected={startDate}
                                    onChange={handleStartDateChange}
                                    dateFormat="MM/dd/yyyy"
                                    className="form-control"
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">End Date:</label>
                            <div className="col-sm-8">
                                <DatePicker
                                    selected={endDate}
                                    onChange={handleEndDateChange}
                                    dateFormat="MM/dd/yyyy"
                                    className="form-control"
                                    isClearable
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-4 d-flex align-items-center justify-content-end">
                                <span className="font-weight-bold">Goal Value:</span>
                            </div>
                            <div className="col-sm-8">
                                <input
                                    type="number"
                                    className="form-control"
                                    id="goalValue"
                                    placeholder="Enter goal value"
                                    value={goalValue}
                                    onChange={handleGoalChange}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <div className="col-sm-4 d-flex align-items-center justify-content-end">
                                <span className="font-weight-bold">Metric:</span>
                            </div>
                            <div className="col-sm-8">
                                <select
                                    className="form-control"
                                    id="metricSelect"
                                    value={selectedMetric}
                                    onChange={handleMetricChange}
                                >
                                    <option value="metric1">Metric 1</option>
                                    <option value="metric2">Metric 2</option>
                                    <option value="metric3">Metric 3</option>
                                </select>
                            </div>
                        </div>
                        <div className="text-center mt-3">
                            <button className="btn btn-primary" onClick={handleSetGoal}>
                                Set Goal
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default GoalSetting;
