import React, { useState } from "react";
import { CSVLink } from "react-csv";
import logo from '../logo.svg';
import { Link } from 'react-router-dom';
import { useEffect } from "react";
import Charts from "./Chartsre";
import DrilldownChart from "./DrilldownChart";
import MonthlyWasteChart from "./MonthlyWasteChart";
import EnvironmentalImpactChart from "./EnvironmentalImpactChart";
import WasteReductionStackedChart from "./WasteReductionChart";
const ITEMS_PER_PAGE = 10;
const generateRandomDataForYear = (year) => {
    const startDate = new Date(year, 0, 1);
    const endDate = new Date(year + 1, 0, 1);
    const data = [];

    for (let currentDate = startDate; currentDate < endDate; currentDate.setDate(currentDate.getDate() + 1)) {
        const formattedDate = currentDate.toISOString().split("T")[0];
        const randomValue = Math.floor(Math.random() * 100) + 1; // Random value between 1 and 100

        data.push({ date: formattedDate, value: randomValue });
    }

    return data;
};

const generateRandomData = (count) => {
    const startDate = new Date(2023, 10, 1); // November 1, 2023
    const endDate = new Date(2023, 10, 30); // November 30, 2023

    const data = [];
    const dateRange = endDate.getTime() - startDate.getTime();

    for (let i = 0; i < count; i++) {
        const randomDate = new Date(startDate.getTime() + Math.random() * dateRange);
        const formattedDate = randomDate.toISOString().split("T")[0];
        const randomValue = Math.floor(Math.random() * 100) + 1; // Random value between 1 and 100

        data.push({ date: formattedDate, value: randomValue });
    }

    return data;
};

const Reports = () => {
    const [filter, setFilter] = useState("day");
    const [currentPage, setCurrentPage] = useState(1);
    const [filteredData, setFilteredData] = useState([]);
    const [csvData, setCsvData] = useState([]);
    const [csvHeaders] = useState([{ label: "Date", key: "date" }, { label: "Value", key: "value" }]);

    useEffect(() => {
        // Generate initial dataset with 150 data points
        const initialData = generateRandomData(150);
        setFilteredData(initialData);
        setCsvData([csvHeaders, ...initialData.map(({ date, value }) => [date, value])]);
    }, [csvHeaders]);

    const handleFilterChange = (newFilter) => {
        setFilter(newFilter);
        setCurrentPage(1);

        // Apply date filtering based on the selected filter
        let filteredData;
        if (newFilter === "day" || newFilter === "week" || newFilter === "month") {
            const currentDate = new Date();
            const filteredDates = {
                day: currentDate.toISOString().split("T")[0],
                week: new Date(currentDate - 7 * 24 * 60 * 60 * 1000).toISOString().split("T")[0],
                month: new Date(currentDate.getFullYear(), currentDate.getMonth(), 1).toISOString().split("T")[0],
            };

            filteredData = generateRandomData(150).filter((item) => item.date >= filteredDates[newFilter]);
        } else if (newFilter === "year") {
            // Generate random data for multiple years
            const startYear = 2020; // Change as needed
            const endYear = new Date().getFullYear();
            filteredData = [];

            for (let year = startYear; year <= endYear; year++) {
                filteredData = [...filteredData, ...generateRandomDataForYear(year)];
            }
        }

        setFilteredData(filteredData);
        setCsvData([csvHeaders, ...filteredData.map(({ date, value }) => [date, value])]);
    };


    // Paginate data
    const indexOfLastItem = currentPage * ITEMS_PER_PAGE;
    const indexOfFirstItem = indexOfLastItem - ITEMS_PER_PAGE;
    const currentData = filteredData.slice(indexOfFirstItem, indexOfLastItem);

    // Change page
    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>
                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/home">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/reports">
                            Reports
                        </Link>
                    </li>
                </ul>

                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>
            <div className="container mt-5">
                <h4 className="text-center">Reports</h4>
                <br></br>
                <div className="row">
                    <div className="col-6">
                        <Charts></Charts>
                    </div><div className="col-6">
                        <DrilldownChart ></DrilldownChart>
                    </div>
                </div>
                <div className="row">
                    <div className="col-6">
                        <MonthlyWasteChart></MonthlyWasteChart>
                    </div><div className="col-6">
                        <WasteReductionStackedChart></WasteReductionStackedChart>
                    </div>
                </div>

                {/* Filter Buttons and Export Button - Align to right */}
                <div className="row justify-content-between mb-3">
                    <div className="col-auto">
                        <button className={`btn btn-outline-secondary ${filter === "day" ? "active" : ""}`} onClick={() => handleFilterChange("day")}>
                            Day
                        </button>
                        <button className={`btn btn-outline-secondary ${filter === "week" ? "active" : ""}`} onClick={() => handleFilterChange("week")}>
                            Week
                        </button>
                        <button className={`btn btn-outline-secondary ${filter === "month" ? "active" : ""}`} onClick={() => handleFilterChange("month")}>
                            Month
                        </button>
                        <button className={`btn btn-outline-secondary ${filter === "year" ? "active" : ""}`} onClick={() => handleFilterChange("year")}>
                            Year
                        </button>
                    </div>
                    <div className="col-auto">
                        {/* Export CSV Button */}
                        <CSVLink data={csvData} headers={csvHeaders} filename="reports.csv" className="btn btn-outline-secondary">
                            Export CSV
                        </CSVLink>
                    </div>
                </div>

                {/* Bootstrap Table with Pagination */}
                <table className="table">
                    <thead>
                        <tr>
                            <th>Date</th>
                            <th>Value</th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentData.map((item, index) => (
                            <tr key={index}>
                                <td>{item.date}</td>
                                <td>{item.value}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>

                {/* Pagination */}
                <nav>
                    <ul className="pagination">
                        {Array.from({ length: Math.ceil(filteredData.length / ITEMS_PER_PAGE) }).map((_, index) => (
                            <li key={index} className={`page-item ${currentPage === index + 1 ? "active" : ""}`}>
                                <button className="page-link" onClick={() => paginate(index + 1)}>
                                    {index + 1}
                                </button>
                            </li>
                        ))}
                    </ul>
                </nav>
            </div>
        </div>

    );
};

export default Reports;
