import React from 'react';
import { Bar } from 'react-chartjs-2';

const WasteReductionStackedChart = () => {
  // Sample data for waste reduction progress
  const data = {
    labels: ['Month 1', 'Month 2', 'Month 3', 'Month 4', 'Month 5', 'Month 6', 'Month 7', 'Month 8', 'Month 9', 'Month 10', 'Month 11', 'Month 12'],
    datasets: [
      {
        label: 'Waste Reduced',
        backgroundColor: '#36A2EB',
        data: [5, 12, 20, 8, 15, 10, 18, 6, 14, 22, 9, 16],
      },
      {
        label: 'Remaining',
        backgroundColor: '#FFCE56',
        data: [95, 88, 80, 92, 85, 90, 82, 94, 86, 78, 91, 84],
      },
      // Add more datasets as needed
    ],
  };

  const options = {
    scales: {
      x: { stacked: true },
      y: { stacked: true },
    },
  };

  return (
    <div>
      <h2>Waste Reduction Progress (Stacked Bar Chart)</h2>
      <Bar data={data} options={options} />
    </div>
  );
};

export default WasteReductionStackedChart;
