import React from 'react';
import Table from 'react-bootstrap/Table';
import Pagination from 'react-bootstrap/Pagination';

const WeightData = [
    { date: '2023-10-28', time: '15:30', weight: '250 g' },
    { date: '2023-10-27', time: '14:45', weight: '500 g' },
    { date: '2023-10-28', time: '01:00', weight: '250 g' },
    { date: '2023-10-28', time: '02:00', weight: '150 g' },
    { date: '2023-10-28', time: '03:00', weight: '300 g' },
    { date: '2023-10-28', time: '04:00', weight: '70 g' },
    { date: '2023-10-28', time: '05:00', weight: '400 g' },
    { date: '2023-10-28', time: '06:00', weight: '200 g' },
    { date: '2023-10-28', time: '07:00', weight: '100 g' },
    { date: '2023-10-28', time: '08:00', weight: '90 g' },
    { date: '2023-10-28', time: '09:00', weight: '350 g' },
    { date: '2023-10-28', time: '10:00', weight: '120 g' },
    { date: '2023-10-28', time: '11:00', weight: '50 g' },
    { date: '2023-10-28', time: '12:00', weight: '450 g' },
    { date: '2023-10-28', time: '13:00', weight: '80 g' },
    { date: '2023-10-28', time: '14:00', weight: '900 g' },
    { date: '2023-10-28', time: '15:00', weight: '170 g' },
    // Add more data as needed
  ];

const PageSize = 5; // Number of items per page

function WeightTable() {
  const [currentPage, setCurrentPage] = React.useState(1);

  const indexOfLastItem = currentPage * PageSize;
  const indexOfFirstItem = indexOfLastItem - PageSize;
  const currentItems = WeightData.slice(indexOfFirstItem, indexOfLastItem);

  const totalItems = WeightData.length;

  const paginate = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  return (
    <div>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Date and Time</th>
            <th>Weight</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {currentItems.map((item) => (
            <tr key={item.date + item.time}>
              <td>{item.date} {item.time}</td>
              <td>{item.weight}</td>
              <td>
                <button className="btn btn-primary">Edit</button> &nbsp;
                <button className="btn btn-danger">Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      <Pagination>
        {Array.from({ length: Math.ceil(totalItems / PageSize) }).map((_, index) => (
          <Pagination.Item
            key={index}
            active={index + 1 === currentPage}
            onClick={() => paginate(index + 1)}
          >
            {index + 1}
          </Pagination.Item>
        ))}
      </Pagination>
    </div>
  );
}

export default WeightTable;
