import logo from '../logo.svg';
import { useState } from 'react';

function StoreManagerDashboard() {
  const [pin, setPin] = useState("");

  const redirectToDashboard = function () {
    if (pin === "1234") {
      window.location.href = "dashboard-store-staff.html";
    } else if (pin === "5678") {
      window.location.href = "dashboard-store-manager.html";
    } else if (pin === "9876") {
      window.location.href = "dashboard-esg-executive.html";
    } else {
      alert("Invalid PIN. Please try again.");
    }
  }

  return (

    <div className="App">
      <header class="bg-light">
        <nav class="navbar navbar-expand-lg navbar-light container">
            <a class="navbar-brand" href="#">
                <img src={logo} class="Logo" alt="IKEA Foods Logo"/>
            </a>
            <div class="user-info ml-auto d-none d-lg-block">
                <p><strong>Username:</strong> Joe, Steven</p>
                <p><strong>Role:</strong>ESG Executive</p>
            </div>
        </nav>
    </header>
    <main class="container mt-3">
        <div class="row">
            <div class="col-md-3">
                <div class="sidebar">
                    <ul class="nav flex-column">
                        <li class="nav-item">
                            <a class="nav-link" href="#">Home Dashboard</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="goal-page.html">Goals Page</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="baseline-page.html">Baseline Page</a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="col-md-9">
                <div class="breadcrumbs">
                    <p><a href="#">Home</a> &gt; ESG Executive Dashboard</p>
                </div>
                <section id="esg-executive-dashboard">
                    <h2>ESG Executive Dashboard</h2>
                </section>
            </div>
        </div>
    </main>

    <footer class="text-center py-2">
        &copy; 2023 Food Waste Management System
    </footer>
    </div>
  );
}

export default StoreManagerDashboard;
