import logo from '../logo.svg';
import { Link } from 'react-router-dom';
import { } from 'chart.js';
import MyChart from './chart';
import Heatmap from './heatmap';
import WasteWeightChart from './TransactionChart';

function EsgDashboard() {
    const openNewTab = () => {
        window.open('/FoodWasteCapture', '_blank');
    };
    return (
        <div className="App">
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>
                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/home">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/reports">
                            Reports
                        </Link>
                    </li>
                </ul>
                
                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>
            <div className="row">
                <div className="col-md-12">
                    <div className="breadcrumbs">
                        <p><a href="#">Home</a> &gt; ESG Executive Dashboard</p>
                    </div>

                    <div className="esg-executive-dashboard text-center">
                        <h4>ESG Executive Dashboard</h4>
                    </div>
                    <div className="row">

                        <div className='col-6'>
                            <MyChart></MyChart>
                        </div>
                        <div className='col-6'>
                            <Heatmap></Heatmap>
                        </div>
                    </div>

                    <div className="row">

                        <div>
                            <WasteWeightChart />
                        </div>
                        {/* <div className='col-6'>
                            <Heatmap></Heatmap>
                        </div> */}
                    </div>
                </div>
            </div>


        </div>
    );
}

export default EsgDashboard;
