import React, { useState } from "react";
import logo from '../logo.svg';
import { Link } from "react-router-dom";

function SetupScreen() {
    const [selectedOption, setSelectedOption] = useState("manual");
    const [connectionType, setConnectionType] = useState("");
    const [availableNetworks, setAvailableNetworks] = useState([]);
    const [selectedNetwork, setSelectedNetwork] = useState("");
    const [binWeight, setBinWeight] = useState("");

    const handleRadioChange = (event) => {
        setSelectedOption(event.target.value);
    };

    const handleConnectionChange = (event) => {
        setConnectionType(event.target.value);
        setSelectedNetwork(""); // Reset selected network when changing the connection type
        fetchAvailableNetworks();
    };

    const handleNetworkSelect = (network) => {
        setSelectedNetwork(network);
    };

    // Simulated data for available WiFi networks
    const hardcodedData = {
        wifiNetworks: ["WiFi Network 1", "WiFi Network 2", "WiFi Network 3"],
        bluetoothDevices: ["Device 1", "Device 2", "Device 3"],
    };

    // Simulate fetching available WiFi networks
    const fetchAvailableNetworks = () => {
        if (connectionType == "wifi") {
            setAvailableNetworks(hardcodedData.bluetoothDevices);
        } else if (connectionType == "bluetooth") {
            setAvailableNetworks(hardcodedData.wifiNetworks);
        } else {
            // setAvailableNetworks([]);
        }
    };
    const handleConnect = () => {
        // Handle the connection logic here
    };
    const openNewTab = () => {
        window.open('/FoodWasteCapture', '_blank');
    };
    const formStyle = {
        border: "1px solid grey",
        borderRadius: "5px",
        margin: "5px",
        padding: "15px",
    };
    const handleBinWeightChange = (event) => {
        setBinWeight(event.target.value);
      };
    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>
                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/homepage">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/setup">
                            Setup
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#" onClick={openNewTab}>Food waste capture</a>
                    </li>
                </ul>
                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>
            <div className="container mt-5 d-flex justify-content-center text-center">
                <div className="col-md-6" style={formStyle}>
                    <h2 className="text-center">Setup </h2>
                    <form>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold">Entry Type:</label>
                            <div className="col-sm-8 text-center">
                                <div className="form-check form-check-inline">
                                    <input
                                        className="form-check-input"
                                        type="radio"
                                        id="manualRadio"
                                        value="manual"
                                        checked={selectedOption === "manual"}
                                        onChange={handleRadioChange}
                                    />
                                    <label className="form-check-label">Manual</label>
                                </div>
                                <div className="form-check form-check-inline">
                                    <input
                                        className="form-check-input"
                                        type="radio"
                                        id="automaticRadio"
                                        value="automatic"
                                        checked={selectedOption === "automatic"}
                                        onChange={handleRadioChange}
                                    />
                                    <label className="form-check-label">Automatic</label>
                                </div>
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold">Bin Weight (lbs):</label>
                            <div className="col-sm-8">
                                <input
                                    type="number"
                                    className="form-control"
                                    id="binWeight"
                                    placeholder="Enter bin weight"
                                    value={binWeight}
                                    onChange={handleBinWeightChange}
                                />
                            </div>
                        </div>

                        {selectedOption === "automatic" && (
                            <div className="form-group row">
                                <label className="col-form-label col-sm-4 font-weight-bold">Connection Type:</label>
                                <div className="col-sm-8 text-center">
                                    <div className="form-check form-check-inline">
                                        <input
                                            className="form-check-input"
                                            type="radio"
                                            id="wifiRadio"
                                            value="wifi"
                                            checked={connectionType === "wifi"}
                                            onChange={handleConnectionChange}
                                        />
                                        <label className="form-check-label">WiFi</label>
                                    </div>
                                    <div className="form-check form-check-inline">
                                        <input
                                            className="form-check-input"
                                            type="radio"
                                            id="bluetoothRadio"
                                            value="bluetooth"
                                            checked={connectionType === "bluetooth"}
                                            onChange={handleConnectionChange}
                                        />
                                        <label className="form-check-label">Bluetooth</label>
                                    </div>
                                </div>
                            </div>
                        )}

                        {connectionType === "wifi" && (
                            <div>
                                <h5>Available WiFi Networks:</h5>
                                <ul className="list-group">
                                    {availableNetworks.map((network, index) => (
                                        <li
                                            key={index}
                                            className={`list-group-item ${selectedNetwork === network ? "active" : ""}`}
                                            onClick={() => handleNetworkSelect(network)}
                                        >
                                            {network}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        )}
                        {connectionType === "bluetooth" && (
                            <div>
                                <h5>Available Bluetooth Devices:</h5>
                                <ul className="list-group">
                                    {availableNetworks.map((device, index) => (
                                        <li
                                            key={index}
                                            className={`list-group-item ${selectedNetwork === device ? "active" : ""}`}
                                            onClick={() => handleNetworkSelect(device)}
                                        >
                                            {device}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        )}
                        {(selectedNetwork) && (
                            <div className="text-center mt-3">
                                <button className="btn btn-primary" onClick={handleConnect}>
                                    Connect
                                </button>
                            </div>
                        )}
                    </form>
                </div>
            </div>
        </>
    );
}

export default SetupScreen;
