import React, { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import logo from '../logo.svg';
import { Link, useNavigate } from "react-router-dom";
function BaselineSetup() {
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(null);
    const [baselineValue, setBaselineValue] = useState("");
    const [selectedUOM, setSelectedUOM] = useState("grams"); // Default UOM
    const navigate = useNavigate();

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };

    const openNewTab = () => {
        window.open('/FoodWasteCapture', '_blank');
    };
    const openLink = (link) => {
        navigate(link);
    };
    const handleEndDateChange = (date) => {
        setEndDate(date);
    };

    const handleBaselineChange = (event) => {
        setBaselineValue(event.target.value);
    };

    const handleUOMChange = (event) => {
        setSelectedUOM(event.target.value);
    };

    const handleSetBaseline = () => {
        // Handle setting the baseline logic here
        // You can access startDate, endDate, baselineValue, and selectedUOM for further processing
        // For example: console.log(startDate, endDate, baselineValue, selectedUOM);
        // navigate("/"); // Navigate back to the home page or any other page after setting baseline
    };

    return (
        <div className="App">

            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>

                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/homepage">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/setup">
                            Setup
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#" onClick={openNewTab}>Food waste capture</a>
                    </li>
                </ul>

                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>
            <div className="container mt-5 d-flex justify-content-center align-items-center">
                <div className="col-md-6">
                    <h2 className="text-center">Baseline Setup</h2>
                    <form>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">Start Date:</label>
                            <div className="col-sm-8">
                                <DatePicker
                                    selected={startDate}
                                    onChange={handleStartDateChange}
                                    dateFormat="MM/dd/yyyy"
                                    className="form-control"
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">End Date:</label>
                            <div className="col-sm-8">
                                <DatePicker
                                    selected={endDate}
                                    onChange={handleEndDateChange}
                                    dateFormat="MM/dd/yyyy"
                                    className="form-control"
                                    isClearable
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">Baseline Value:</label>
                            <div className="col-sm-8">
                                <input
                                    type="number"
                                    className="form-control"
                                    id="baselineValue"
                                    placeholder="Enter baseline value"
                                    value={baselineValue}
                                    onChange={handleBaselineChange}
                                />
                            </div>
                        </div>
                        <div className="form-group row">
                            <label className="col-form-label col-sm-4 font-weight-bold text-right">UOM:</label>
                            <div className="col-sm-8">
                                <select
                                    className="form-control"
                                    id="uomSelect"
                                    value={selectedUOM}
                                    onChange={handleUOMChange}
                                >
                                    <option value="grams">Grams</option>
                                    <option value="kilograms">Kilograms</option>
                                    <option value="pounds">Pounds</option>
                                </select>
                            </div>
                        </div>
                        <div className="text-center mt-3">
                            <button className="btn btn-primary" onClick={handleSetBaseline}>
                                Set Baseline
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default BaselineSetup;
