import './App.css';
import { useState } from 'react';
import { Route, Routes, useNavigate } from 'react-router-dom';
import { BrowserRouter as Router } from "react-router-dom";
import EsgDashboard from './components/esg-dashboard';
import StoreManagerDashboard from './components/store-manager-dashboard';
import StaffDashboard from './components/staff-dashboard';
import Login from './components/login';
import FoodWasteCapture from './components/weightCapture';
import SetupScreen from './components/SetupScreen';
import BaselineSetup from './components/BaselineSetup';
import GoalSetting from './components/GoalSetting';
import Reports from './components/Reports';

function App() {
  const [pin, setPin] = useState("");
  const navigate = useNavigate();
  const redirectToDashboard = function () {
    if (pin === "1234") {
      navigate('/homepage')
    } else if (pin === "5678") {
      navigate('/dashboard')
    } else if (pin === "9876") {
      navigate('/home')
    } else {
      alert("Invalid PIN. Please try again.");
    }
  }

  return (
    <div>
      <Routes>
        <Route path="/" element={<Login />}></Route>
        <Route path="/home" element={<EsgDashboard />}></Route>
        <Route path="/dashboard" element={<StoreManagerDashboard />}></Route>
        <Route path="/homepage" element={<StaffDashboard />}></Route>
        <Route path="/FoodWasteCapture" element={<FoodWasteCapture />}></Route>
        <Route path="/setup" element={<SetupScreen />}></Route>
        <Route path="/goal-setting" element={<GoalSetting />} />
        <Route path="/baseline-setting" element={<BaselineSetup />} />
        <Route path="/reports" element={<Reports />} />
      </Routes>
      <footer className="text-center fixed-bottom">
        &copy; 2023 Food Waste Management System
      </footer>
    </div>
  );
}

export default App;
