// VirtualKeyboard.js
import React from 'react';

const VirtualKeyboard = ({ onKeyPress }) => {
    const keys = [
        '1', '2', '3',
        '4', '5', '6',
        '7', '8', '9',
        '.', '0', 'Delete',
    ];

    const handleKeyPress = (key) => {
        onKeyPress(key);
    };

    const createKeyboardRows = () => {

        const keyboardRows = [];
        for (let i = 0; i < keys.length; i += 4) {
            const rowKeys = keys.slice(i, i + 4);
            const keyboardRow = (
                <>
                    <div key={i} className="row">
                        {rowKeys.map((key, index) => (
                            <div key={key} className="col-3">
                                <button
                                    className={`btn btn-outline-secondary btn-lg w-100 h-100 numeric-key no-space`}
                                    onClick={() => handleKeyPress(key)}
                                >
                                    {key}
                                </button>
                            </div>
                        ))}

                    </div>

                </>
            );
            keyboardRows.push(keyboardRow);
        }
        return keyboardRows;
    };

    return (
        <div className="virtual-keyboard">
            {createKeyboardRows()}
            <br></br>
            <div className="row text-center">
                <br></br>
                <div className="col-6 justify-content-center">
                    <button type="button" className="btn btn-secondary btn-lg">Clear</button>
                </div>
                <div className="col-6 justify-content-center">
                    <button type="submit" className="btn btn-primary btn-lg">Submit</button>
                </div>

            </div>
        </div>
    );
};

export default VirtualKeyboard;
