import logo from '../logo.svg';
import '../App.css';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function Login() {
  const [pin, setPin] = useState("");
  const navigate = useNavigate();
  const redirectToDashboard = function () {
    if (pin === "1234") {
      navigate('/homepage')
    } else if (pin === "5678") {
      navigate('/dashboard')
    } else if (pin === "9876") {
      navigate('/home')
    } else {
      alert("Invalid PIN. Please try again.");
    }
  }

  return (

    <div className="App">
     
      <div class="container">
        <img src={logo} alt="IKEA Foods Logo" class="logo" />
        <div class="login-box">
          <h1>Login</h1>
          <ul id="pin-reference">
            <li><strong>Store Staff PIN:</strong> 1234</li>
            <li><strong>Store Manager PIN:</strong> 5678</li>
            <li><strong>ESG Executive PIN:</strong> 9876</li>
          </ul>
          <form onSubmit={redirectToDashboard}>
            <div class="input-group">
              <input type="text" id="pin" value={pin}
                onChange={(e) => setPin(e.target.value)} placeholder="Enter your PIN" required />
            </div>
            <div class="input-group">
              <button className='btn btn-primary' type="submit" value="Login">Login</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Login;
