import logo from '../logo.svg';
import { useState } from 'react';
import MyChart from './chart';
import Heatmap from './heatmap';
import WasteWeightChart from './TransactionChart';
import { Link, useNavigate } from "react-router-dom";
function StaffDashboard() {
    const [pin, setPin] = useState("");

    const redirectToDashboard = function () {
        if (pin === "1234") {
            window.location.href = "dashboard-store-staff.html";
        } else if (pin === "5678") {
            window.location.href = "dashboard-store-manager.html";
        } else if (pin === "9876") {
            window.location.href = "dashboard-esg-executive.html";
        } else {
            alert("Invalid PIN. Please try again.");
        }
    }
    const navigate = useNavigate();
    const navigateToBaselineSetup = () => {
        navigate("/setbaseline");
    };
    const navigateToGoal = () => {
        navigate("/set-goal ");
    };

    const openNewTab = () => {
        window.open('/FoodWasteCapture', '_blank');
    };
    const openLink = () => {
        window.open('/setbaseline');
    };
    return (

        <div className="App">

            <nav className="navbar navbar-expand-lg navbar-light bg-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>

                <ul className="navbar-nav col-9">
                    <li className="nav-item">
                        <Link className="nav-link" to="/homepage">
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/goal-setting">
                            Goal Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/baseline-setting">
                            Baseline Setting
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" to="/setup">
                            Setup
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#" onClick={openNewTab}>Food waste capture</a>
                    </li>
                </ul>
                <div className="navbar-text col-3 float-right">
                    <span className="username">Joe</span> &nbsp;
                    <button className="btn btn-outline-danger btn-sm ml-2">Sign Out</button>
                </div>
            </nav>
            <div className="row">
                <div className="col-md-12">
                    <div className="breadcrumbs">
                        <p><a href="#">Home</a> &gt; Store Dashboard</p>
                    </div>

                    <div className="esg-executive-dashboard text-center">
                        <h4>Store Dashboard</h4>
                    </div>
                    <div className="row">

                        <div className='col-6'>
                            <MyChart></MyChart>
                        </div>
                        <div className='col-6'>
                            <Heatmap></Heatmap>
                        </div>
                    </div>

                    <div className="row">

                        <div>
                            <WasteWeightChart />
                        </div>

                    </div>
                </div>
            </div>


        </div>
    );
}

export default StaffDashboard;
