import React, { useState, Component } from 'react';
import logo from '../logo.svg';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import './custom.css'
import VirtualKeyboard from './VirtualKeyboard';
import WeightTable from './weightcaptured';
const FoodWasteCapture = () => {
    const [selectedDate, setSelectedDate] = useState(null);
    const [quantity, setQuantity] = useState('');
    const [selectedUOM, setSelectedUOM] = useState('grams'); // Default UOM
    const [reason, setReason] = useState('');
    const [submittedData, setSubmittedData] = useState(null);
    const handleDateChange = (newDate) => {
        setSelectedDate(newDate);
    };

    function generateRandomData() {
        const data = [];

        // Generate random weight measurements for each hour in a day
        for (let hour = 0; hour < 24; hour++) {
            const date = new Date();
            date.setHours(hour, 0, 0, 0);

            // Generate a random weight between 0 and 15 grams
            const weight = (Math.random() * 15).toFixed(2); // Adjust the range as needed

            data.push({
                date,
                time: `${hour.toString().padStart(2, '0')}:00`,
                weight: `${weight} g`,
            });
        }

        return data;
    }
    const handleFormSubmit = (e) => {
        e.preventDefault();

        // Create an object with the captured data
        const wasteData = {
            quantity: `${quantity} ${selectedUOM}`,
            reason,
        };

        // You can send this data to a server, store it, or process it as needed
        // For now, let's just display it in the component
        setSubmittedData(wasteData);
    };

    return (
        <div>
            <nav class="navbar navbar-expand-lg navbar-custom">
                <a class="navbar-brand" href="#">
                    <img src={logo} alt='IKEA Logo' />
                </a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                </div>
            </nav>

            <div className="row justify-content-center">
                <div className="col-6 ">
                    <div className='text-center '><b>Food Waste Capture</b></div>
                    <form onSubmit={handleFormSubmit}>
                        <label htmlFor="quantity text-center">Quantity *:</label>
                        <div className="form-group border-2 borderForform p-10">
                            <div className="row">
                                <div className="col-8">
                                    <input
                                        type="number"
                                        className="form-control h-50p"
                                        id="quantity"
                                        value={quantity}
                                        onChange={(e) => setQuantity(e.target.value)}
                                        required
                                    />
                                </div>
                                <div className="col-4">
                                    <select className="form-control" id="uom">
                                        <option value="grams">Grams</option>
                                        <option value="kilograms">Kilograms</option>
                                        <option value="pounds">Pounds</option>
                                        {/* Add more UOM options as needed */}
                                    </select>
                                </div>
                            </div>

                            <span className='font-15'>Frequently used:</span>
                            <div class="mt-3 text-center">
                                <div class="btn-group">
                                    <button class="btn btn-outline-primary" onclick="fillWeight('100')">100g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('200')">200g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('250')">250g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('300')">300g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('400')">400g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('500')">500g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('600')">600g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('700')">700g</button>
                                    <button class="btn btn-outline-primary" onclick="fillWeight('750')">750g</button>
                                </div>
                            </div>
                            <br></br>
                            <VirtualKeyboard></VirtualKeyboard>
                        </div>
                        <br></br>
                    </form>
                </div>
                <div className='col-6'>
                    <div className='text-center '><b>Todays Transactions</b></div>
                    <br></br>
                    <WeightTable></WeightTable>
                </div>
            </div>
        </div>
    );
};

export default FoodWasteCapture;
