import React, { useState } from "react";
import { Bar, Pie } from "react-chartjs-2";

const DrilldownChart = () => {
  const [chartData, setChartData] = useState({
    labels: ["Sweden", "France", "England", "India", "America"],
    datasets: [
      {
        data: [30, 40, 25, 50, 35],
        backgroundColor: ["blue", "green", "orange", "purple", "yellow"],
        drilldown: {
          "Sweden": ["State 1", "State 2"],
          "France": ["State 3", "State 4"],
          "England": ["State 5", "State 6"],
          "India": ["State 7", "State 8"],
          "America": ["State 9", "State 10"],
        },
      },
    ],
  });

  const generateRandomData = (count) => {
    return Array.from({ length: count }, () => Math.floor(Math.random() * 50) + 10);
  };

  const handleDrilldown = (itemIndex) => {
    const selectedCountry = chartData.labels[itemIndex];
    const stateDrilldown = chartData.drilldown[selectedCountry];

    if (stateDrilldown) {
      // Generate random data for states (replace this with your actual data fetching logic)
      const stateData = generateRandomData(stateDrilldown.length);

      // Fetch data for the selected drilldown (replace this with your actual API/data fetching logic)
      const newData = {
        labels: stateDrilldown.map((state, index) => `${selectedCountry} - ${state}`),
        datasets: [
          {
            data: generateRandomData(stateDrilldown.length),
            backgroundColor: stateData.map((_, index) => `orange`),
            drilldown: stateData.map((_, index) => `City ${index + 1}`),
          },
        ],
      };

      setChartData(newData);
    } else {
      console.error(`Drilldown data not available for ${selectedCountry}`);
    }
  };

  const handleBack = () => {
    // Reset to the previous level (replace this with your actual logic)
    setChartData({
      labels: ["Sweden", "France", "England", "India", "America"],
      datasets: [
        {
          data: [30, 40, 25, 50, 35],
          backgroundColor: ["blue", "green", "orange", "purple", "yellow"],
          drilldown: {
            "Sweden": ["State 1", "State 2"],
            "France": ["State 3", "State 4"],
            "England": ["State 5", "State 6"],
            "India": ["State 7", "State 8"],
            "America": ["State 9", "State 10"],
          },
        },
      ],
    });
  };

  return (
    <div>
      <h5>Drilldown Chart</h5>
      <Bar
        data={chartData}
        options={{
          onClick: (e, elements) => {
            if (elements.length > 0) {
              const itemIndex = elements[0]._index;
              if (chartData.labels.length === 5) {
                handleDrilldown(itemIndex); // Country level
              }
            }
          },
        }}
      />
      <button onClick={handleBack}>Back</button>
    </div>
  );
};

export default DrilldownChart;
